import { useState, useRef } from 'react';
import styled from 'styled-components';

interface TextAreaProps {
    /**
     * label description.
     */
    label?: string;

    /**
     * isRequired description.
     */
    isRequired?: boolean;

    /**
     * Textarea height.
     * @default '136px'
     */
    height?: string;
}

interface WrapperProps {
    focused: boolean;
    expanded: boolean;
    height: string;
}

/**
 * Todo
 * Add focus outline
 * Add color variations
 */
const Wrapper = styled.div<WrapperProps>`
    max-width: 600px;
    height: ${({ height }) => height};
    margin-bottom: 1rem;
    padding: 15px;
    border-radius: 6px;
    font-size: 16px;
    cursor: text;
    background-color: rgba(0, 0, 0, 0.3);
    transition: all 0.2s ease-in-out;
    display: flex;
    flex-direction: column;

    ${({ focused, expanded }) =>
        (focused || expanded) &&
        `padding-top: 10px;
        border: 1px solid black; 
        background-color: var(--color-white);
        `}

    label {
        cursor: inherit;
        transition: inherit;

        ${({ focused, expanded }) =>
            (focused || expanded) &&
            `font-size: 12px;
            font-weight: bold;`}

        // required text
        i {
            font-style: normal;
            font-size: 12px;
            font-weight: normal;
            padding-left: 5px;
        }
    }

    textarea {
        all: unset;
        display: block;
        transition: inherit;
        will-change: height;
        resize: none;
        width: 100%;
        height: 100%;
        flex-grow: 1;

        ${({ focused, expanded }) =>
            (focused || expanded) && `height: 1.25em;`};
    }
`;

/**
 * TextArea component
 */
export const TextArea = ({
    label,
    isRequired,
    height = '136px',
}: TextAreaProps) => {
    const [focused, setFocused] = useState(false);
    const [expanded, setExpanded] = useState(false);
    const textareaRef = useRef<HTMLTextAreaElement>(null);

    const onFocus = () => setFocused(true);
    const onBlur = () => setFocused(false);

    const handleTextArea = () => {
        if (!textareaRef.current) return;
        setExpanded(textareaRef.current.value.length > 0);
    };

    return (
        <Wrapper
            focused={focused}
            expanded={expanded}
            height={height}
            onClick={() => textareaRef?.current?.focus()}
        >
            {label && (
                <label>
                    {label}
                    {!isRequired && <i>(optional)</i>}
                </label>
            )}
            <textarea
                required={isRequired}
                onFocus={onFocus}
                onBlur={onBlur}
                onChange={handleTextArea}
                ref={textareaRef}
            ></textarea>
        </Wrapper>
    );
};
